import React from 'react';

import { useFormik } from 'formik/dist/Formik';

import { renderTimeViewClock } from '@mui/x-date-pickers/timeViewRenderers';
import Box from '@mui/material/Box';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';

export default function DateTimeFieldControl({
  fieldName,
  label,
  disablePast,
  disableFuture,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly label: string;
  readonly disablePast?: boolean;
  readonly disableFuture?: boolean;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const textFieldRef = React.useRef<HTMLElement | null>(null);
  const [isFocused, setIsFocused] = React.useState<boolean>(false);
  // console.log('Ref', textFieldRef.current);
  return (
    <Box key={fieldName} sx={{ height: '75px' }}>
      <DateTimePicker
        label={label}
        ampm={false}
        disablePast={disablePast}
        disableFuture={disableFuture}
        disabled={disabled}
        viewRenderers={{
          hours: renderTimeViewClock,
          minutes: renderTimeViewClock,
          seconds: renderTimeViewClock,
        }}
        slotProps={{
          openPickerIcon: {
            onFocus: () => {
              // console.log('FocusIcon');
            },
            onBlur: (event: any) => {
              // console.log('BlurIcon');
            },
          },
          textField: {
            inputRef: (el) => {
              // console.log('Get Ref', el);
              textFieldRef.current = el;
            },
            fullWidth: true,
            // focused: isFocused,
            onFocus: () => {
              // console.log('Focus');
              setIsFocused(true);
            },
            size: 'small',
            id: fieldName,
            name: fieldName,
            onBlur: (event) => {
              // console.log('Blur');
              setIsFocused(false);
              formik.handleBlur(event);
            },
            error:
              formik.touched[fieldName] && Boolean(formik.errors[fieldName]),
            helperText:
              formik.touched[fieldName] && `${formik.errors[fieldName] || ''}`,
          },
        }}
        value={
          typeof formik.values[fieldName] === 'string'
            ? new Date(formik.values[fieldName])
            : formik.values[fieldName]
        }
        onChange={(value) => formik.setFieldValue(fieldName, value, true)}
        // onClose={() => formik.setFieldTouched(fieldName, true, true)}
        onOpen={() => {
          // console.log('onOpen');
          // setIsFocused((prev) => !prev && true);
          // console.log('Open', textFieldRef.current);
          textFieldRef.current && (textFieldRef.current as HTMLElement).focus();
        }}
        onClose={() => {
          // console.log('onClose');
          queueMicrotask(() => formik.setFieldTouched(fieldName, true, true));
          // reason !== 'blur' &&
          //   textFieldInputRef.current &&
          //   (textFieldInputRef.current as HTMLElement).blur();
        }}
      />
    </Box>
  );
}
